.linechart-container{
  display: inline-block;
  position: relative;
  width: 100%;
  margin: 50px 0;
}
.linechart-container:first-of-type{
  margin: 0 0 50px;
}

.linechart-graph-container{
  display: inline-block;
  position: relative;
  top: 25px;
  width: 100%;
}
@media screen and (min-width: 40em) {
  .linechart-graph-container{
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }
}

.linechart-name-container{
  display: inline-block;
  position: relative;
  margin: 0rem auto 1rem;
  line-height: 20px;
  color: rgb(102, 102, 102);
}
@media screen and (min-width: 40em) {
  .linechart-name-container{
    flex-basis: 10%;
  }
}

.linechart-daybutton{
  width: 100px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #BADBD1;
  background-color: #FFF;
  margin: 0 auto;
  cursor: pointer;
}

.linechart-daybutton-wrapper{
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
}

.linechart-dayicon-wrapper{
  margin-left: 5px;
}

.linechart-daydropdown{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  width: 100px;
  border: 1px solid #BADBD1;
  box-sizing: border-box;
  z-index: 100;
  cursor: pointer;
}

.linechart-dayselect{
  padding: 3px;
  background-color: #FFF;
}
.linechart-dayselect:hover{
  background-color: #96ADC8;
}

.linechart-name{
  position: relative;
  top: 5px;
}

@media screen and (min-width: 40em) {
  .linechart-wrapper{
    flex-basis: 90%;
  }
}

.linechart-configurations-container{
  position: relative;
  top: 50px;
  max-width: 100%;
}

.linechart-dates-container{
  display: flex;
  width: 90%;
  flex-wrap: nowrap;
  align-items: center;
  /* margin: 50px auto 0; */
  float: right;
}
@media screen and (max-width: 40em) {
  .linechart-dates-container{
    width: 100%;
    float: none;
  }
}

.linechart-date-container{
  flex-basis: 50%;
}

.linechart-date-label{
  position: relative;
  margin: 0 5px;
  line-height: 30px;
  vertical-align: bottom;
}

.linechart-startdatepicker, .linechart-enddatepicker{
  width: 100px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #BADBD1;
  background-color: #FFF;
  margin: 0 auto;
  text-align: center;
  font-size: 16px;
}

.linechart-datatable-container{
  display: inline-block;
  position: relative;
  margin: 15px 0;
  width: 90%;
  float: right;
  height: auto;
}
@media screen and (max-width: 40em) {
  .linechart-datatable-container{
    width: 100%;
    float: none;
  }
}

.linechart-datatablebutton{
  width: 150px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #BADBD1;
  background-color: #FFF;
  margin: 0 auto 10px;
  cursor: pointer;
}

.linechart-datatablevalue{
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
}

.linechart-similarcountry-area{
  display: inline-block;
  position: relative;
  width: 90%;
  float: right;
}
@media screen and (max-width: 40em) {
  .linechart-similarcountry-area{
    width: 100%;
    float: none;
  }
}

.linechart-similarcountry-area-text{
  display: block;
}

.linechart-similarcountry-button{
  display: inline-block;
  position: relative;
  width: auto;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #BADBD1;
  background-color: #FFF;
  margin: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

.linechart-similarcountry-button-currentcountry{
  color: #FFF !important;
  background-color: #70161E !important;
  cursor: auto !important;
}

.linechart-similarcountry-button-text{
  display: inline-block;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  /* vertical-align: middle; */
}
