.app-container {
  text-align: center;
  margin-bottom: 50px;
}

.country-select-container{
  position: relative;
  margin: 0 auto;
}

.country-select{
  position: relative;
  height: 30px;
  max-width: 50%;
  font-size: 16px;
  cursor: pointer;
}

.charts-container {
  display: inline-block;
  position: relative;
  top: 25px;
  width: 90%;
}
@media screen and (max-width: 40em) {
  .charts-container{
    width: 95%;
  }
}

.charts-container-downloading {
  height: 100vh;
  background-color: rgba(0,0,0,0.25);
}

.charts-container-downloading-text, .charts-container-text{
  display: block;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
}
