.barchart-container{
  display: inline-block;
  position: relative;
  margin: 50px 0;
  width: 100%;
}

.barchart-graph-container{
  display: inline-block;
  position: relative;
  top: 25px;
  width: 100%;
}
@media screen and (min-width: 40em) {
  .barchart-graph-container{
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
  }
}

.barchart-name{
  margin: 0rem auto 1rem;
  line-height: 20px;
  color: rgb(102, 102, 102);
}
@media screen and (min-width: 40em) {
  .barchart-name{
    flex-basis: 10%;
  }
}

.barchart-name-select{
  margin: 0 5px;
}

.barchart-wrapper{
  flex-basis: 90%;
}

.barchart-configurations-container{
  position: relative;
  top: 50px;
}

.barchart-dates-container{
  display: flex;
  width: 90%;
  flex-wrap: nowrap;
  align-items: center;
  /* margin: 50px auto 0; */
  float: right;
}

.barchart-date-container{
  flex-basis: 50%;
}

.barchart-date-label{
  position: relative;
  margin: 0 5px;
  line-height: 30px;
  vertical-align: bottom;
}

.barchart-datebutton{
  width: 100px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #BADBD1;
  background-color: #FFF;
  margin: 0 auto;
  cursor: pointer;
}

.barchart-datebutton-wrapper{
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
}

.barchart-startdatepicker, .barchart-enddatepicker{
  width: 100px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #BADBD1;
  background-color: #FFF;
  margin: 0 auto;
  text-align: center;
  font-size: 16px;
}

.barchart-datatable-container {
  display: inline-block;
  position: relative;
  margin: 15px 0;
  width: 90%;
  float: right;
  height: auto;
}

.barchart-datatablebutton{
  width: 150px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #BADBD1;
  background-color: #FFF;
  margin: 0 auto 10px;
  cursor: pointer;
}

.barchart-datatablevalue{
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
}

.barchart-similarcountry-area{
  display: inline-block;
  position: relative;
  width: 90%;
  float: right;
}
@media screen and (max-width: 40em) {
  .barchart-similarcountry-area{
    width: 100%;
    float: none;
  }
}

.barchart-similarcountry-area-text{
  display: block;
}

.barchart-similarcountry-button{
  display: inline-block;
  position: relative;
  width: auto;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #BADBD1;
  background-color: #FFF;
  margin: 5px;
  padding: 5px 10px;
  cursor: pointer;
}

.barchart-similarcountry-button-currentcountry{
  color: #FFF !important;
  background-color: #70161E !important;
}

.barchart-similarcountry-button-text{
  display: inline-block;
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  -webkit-transform: translate(-50%,-50%);
  -ms-transform: translate(-50%,-50%);
  /* vertical-align: middle; */
}
